<template>
    <section v-if="showConfig">
        <!----------------------------------------------------------------------------------------------------->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">

            <el-form :inline="true" :model="filters" ref="filters">

                <el-form-item label="" prop="userIdQuery">
                    <el-input v-model="filters.userIdQuery" placeholder="">
                    </el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" v-on:click="pagingList()" size="small" plain><i
                            class="fa fa-search fa-lg"></i> 











                    </el-button>
                </el-form-item>

                <el-form-item>
                    <el-button type="success" v-on:click="handleReset()"
                               size="small" plain><i class="fa fa-refresh fa-lg"></i> 











                    </el-button>
                </el-form-item>

                <el-form :inline="true">
                    <el-form-item>
                        <el-button type="success" @click="handleSave('')" icon="el-icon-plus" size="small"
                                   title="" plain>
                            
                        </el-button>
                    </el-form-item>

                </el-form>

            </el-form>


        </el-col>
        <!----------------------------------------------------------------------------------------------------->


        <!-----------------------------------------tab_sys_user---------------------------------------------->
        <el-table :data="dataList" :height="tableHigth" v-loading="listLoading" @selection-change="selsChange"
                  highlight-current-row stripe border resizable>
            <el-table-column type="selection"></el-table-column>
            <el-table-column type="index" label="" width="80"></el-table-column>
            <el-table-column prop="userId" show-overflow-tooltip label="" label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column prop="userName" show-overflow-tooltip label="" label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column prop="lastTime" show-overflow-tooltip label="" label-class-name="el-tooltip"
                             :formatter="formatDate">
            </el-table-column>
            <el-table-column prop="userAmount" show-overflow-tooltip label="" label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column prop="userValid" show-overflow-tooltip label="" label-class-name="el-tooltip">
                <template slot-scope="scope">
                    <el-tag
                            :type="scope.row.userValid ? 'primary' : 'danger'"
                            close-transition>{{scope.row.userValid ? '' : ''}}









                    </el-tag>
                </template>
            </el-table-column>
            <el-table-column label="" width="100">
                <template slot-scope="scope">
                    <el-button @click="handlePw(scope.$index, scope.row)" type="text" size="small" title=""><span
                            style="color:#F00"></span></el-button>
                </template>
            </el-table-column>
            <el-table-column label="" width="140" header-align="center" align="center">
                <template slot-scope="scope">
                    <el-button-group>
                        <el-button  type="primary" @click="handleSave(scope.row.userId)" icon="el-icon-edit" title="" size="mini"></el-button>
                        <el-button type="danger" @click="handleDel(scope.$index, scope.row)" icon="el-icon-delete" title="" size="mini"></el-button>
                    </el-button-group>
                </template>
            </el-table-column>
        </el-table>
        <!-----------------------------------------tab_sys_user---------------------------------------------->


        <!----------------------------------------------------------------------------------------------------->
        <el-col :span="24" class="toolbar">
            <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">














            </el-button>
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :page-sizes="[10,15,25,50,100]"
                    :page-size=filters.pageSize
                    :current-page.sync="resetIndex"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total"
                    style="float:right;">
            </el-pagination>
        </el-col>
        <!----------------------------------------------------------------------------------------------------->


        <!---->
        <el-dialog title="" :visible.sync="editPwVisible" label-suffix=":" :close-on-click-modal="false" size="large">
            <el-form :model="editPwForm" ref="editPwForm" label-width="120px" :rules="editPwRules">
                <el-form-item label="" prop="userIdView">
                    <el-input v-model="editPwForm.userIdView" auto-complete="off" :disabled="true"></el-input>
                </el-form-item>
                <el-form-item label="" prop="pwView">
                    <el-input type="password" v-model="editPwForm.pwView" auto-complete="off"></el-input>
                </el-form-item>
                <el-form-item label="" prop="pwConfirmView">
                    <el-input type="password" v-model="editPwForm.pwConfirmView" auto-complete="off"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click.native="editPwVisible = false"></el-button>
                <el-button type="primary" @click.native="editPw" :loading="editPwLoading"></el-button>
            </div>
        </el-dialog>

    </section>

    <section v-else-if="showSave">
        <component @backConfig="backConfig" :userId="userIdQuery" v-bind:is=userSave>
        </component>
    </section>
</template>


<script>
    import tableHeight from '../../../js/heightUtil'
    import dateUtil from '../../../js/dateUtil'
    import baseUtil from '../../../js/baseUtil'
    import {pagingList, remove, editPw} from './userConfig.api'
    import userSave from './userSave'

    export default {
      data () {
        let validatePass = (rule, value, callback) => {
          if (value === '') {
            callback(new Error(''))
          } else {
            if (this.editPwForm.pwView !== '') {
              this.$refs.editPwForm.validateField('pwConfirmView')
            }
            callback()
          }
        }
        let validatePass2 = (rule, value, callback) => {
          if (value === '') {
            callback(new Error(''))
          } else if (value !== this.editPwForm.pwView) {
            callback(new Error('!'))
          } else {
            callback()
          }
        }

        return {
          // 
          userSave: userSave,
          userIdQuery: '',
          showConfig: true,
          showSave: true,

          dataList: [], // 
          listLoading: false, // list
          tableHigth: '', // 
          sels: [], // 
          total: 0, // 
          resetIndex: 1,

          // 
          filters: {
            pageIndex: 0,
            pageSize: 15,
            userIdQuery: ''
          },

          editPwVisible: false, // 
          editPwLoading: false,
          editPwForm: {
            userIdView: '',
            pwView: '',
            pwConfirmView: '',
            userId: '',
            passWord: ''
          },

          editPwRules: {
            pwView: [
              {required: true, message: '', trigger: 'blur'},
              {min: 6, message: '6', trigger: 'blur'},
              {validator: validatePass, trigger: 'blur'}
            ],
            pwConfirmView: [
              {required: true, message: '', trigger: 'blur'},
              {min: 6, message: '6', trigger: 'blur'},
              {validator: validatePass2, trigger: 'blur'}
            ]
          },

          // 
          exportDis: false
        }
      },

      methods: {

        // 
        formatDate: function (row, column) {
          return (!row[column.property] || row[column.property] === '') ? '' : dateUtil.formatDate.format(new Date(row[column.property]), 'yyyy-MM-dd hh:mm:ss')
        },

        /** **********************************************************************************************/
        // 
        pagingList: function () {
          this.listLoading = true
          this.tableHigth = tableHeight(371)
          pagingList(this.filters).then((res) => {
            if (res.error) {
              this.$message({
                showClose: true,
                message: res.error,
                type: 'error'
              })
            } else {
              this.total = res.success.total
              this.dataList = res.success.rows
            }
            this.listLoading = false
          })
        },
        // 
        handleReset () {
          this.$refs['filters'].resetFields()
          this.filters.pageIndex = 0
          this.filters.pageSize = 15
          this.resetIndex = 1
          this.pagingList()
        },
        // 
        handleSizeChange (val) {
          this.filters.pageSize = val
          this.handleCurrentChange(1)
        },
        //  
        handleCurrentChange (val) {
          if (val === 1) {
            this.filters.pageIndex = 0
          } else {
            this.filters.pageIndex = (val - 1) * (this.filters.pageSize)
          }
          this.pagingList()
        },
        /** **********************************************************************************************/

        /** **********************************************************************************************/

        handlePw: function (index, row) {
          this.editPwVisible = true
          this.editPwForm.userIdView = row.userId
        },

        editPw: function () {
          this.$refs.editPwForm.validate((valid) => {
            if (valid) {
              this.$confirm('', '', {}).then(() => {
                this.editPwLoading = true
                this.editPwForm.userId = baseUtil(this.editPwForm.userIdView)
                this.editPwForm.passWord = baseUtil(this.editPwForm.pwView)
                editPw(this.editPwForm).then((res) => {
                  this.editPwLoading = false
                  if (res.error) {
                    this.$message({
                      showClose: true,
                      message: res.error,
                      type: 'error'
                    })
                  } else {
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'success'
                    })
                    this.editPwLoading = false
                    this.$refs['editPwForm'].resetFields()
                  }
                }).catch(e => {
                })
              }).catch(e => {
              })
            }
          })
        },
        /** **********************************************************************************************/

        /** **************************************************************************************************/
        // 
        selsChange: function (sels) {
          this.sels = sels
        },
        handleDel: function (index, row) {
          this.$confirm('?', '', {
            type: 'warning'
          }).then(() => {
            let para = {deleteIds: row.userId}
            remove(para).then((res) => {
              if (res.error) {
                this.$message({
                  showClose: true,
                  message: res.error,
                  type: 'error'
                })
              } else {
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                })
              }
              this.pagingList()
            })
          })
        },
        batchRemove: function () {
          const ids = this.sels.map(item => item.userId).toString()
          this.$confirm('', '', {
            type: 'warning'
          }).then(() => {
            let para = {deleteIds: ids}
            remove(para).then((res) => {
              if (res.error) {
                this.$message({
                  showClose: true,
                  message: res.error,
                  type: 'error'
                })
              } else {
                this.$message({
                  showClose: true,
                  message: '',
                  type: 'success'
                })
              }

              this.pagingList()
            })
          })
        },
        /** **************************************************************************************************/

        /** ***********************************************************************************************/
        handleSave: function (userId) {
          this.userIdQuery = userId
          this.showConfig = false// 
        },
        backConfig: function () {
          this.showConfig = true// 
          this.pagingList()
        }

        /** ***********************************************************************************************/
      },

      created () {
        this.pagingList()
      }
    }
</script>

<style>
    .view-table label {
        color: #99a9bf;
    }

</style>


