<template>
    <section>

        <el-col>
        <el-form :inline="true" class="view-table">

            <el-form-item style="float:right;">
                <el-button type="danger" v-on:click="handleBack(false)" size="small"><i class="fa fa-undo fa-lg"></i> 
                </el-button>
            </el-form-item>
        </el-form>
         </el-col>


        <el-col>
        <el-form ref="saveForm" :model="saveForm" label-width="140px" label-suffix=":" :rules="saveFormRules">

            <el-form-item label="" prop="userId">
                <el-input v-model="saveForm.userId" placeholder="4"></el-input>
            </el-form-item>

            <el-form-item label="" prop="userName">
                <el-input v-model="saveForm.userName" placeholder=""></el-input>
            </el-form-item>

            <el-form-item label="" prop="userDescrip">
                <el-input type="textarea" v-model="saveForm.userDescrip" placeholder=""></el-input>
            </el-form-item>


            <el-form-item label="" prop="displayPassword" v-if="showPassWord">
                <el-input type="password" v-model="saveForm.displayPassword" placeholder="6"></el-input>
            </el-form-item>


            <el-form-item label="" prop="confirmPassword" v-if="showPassWord">
                <el-input type="password" v-model="saveForm.confirmPassword"
                          placeholder="6"></el-input>
            </el-form-item>


            <el-form-item label="">
                <el-radio-group v-model="saveForm.userSex">
                    <el-radio :label=true></el-radio>
                    <el-radio :label=false></el-radio>
                </el-radio-group>
            </el-form-item>

            <el-form-item label="">
                <el-input v-model="saveForm.userTel" placeholder=""></el-input>
            </el-form-item>

            <el-form-item label="">
                <el-input v-model="saveForm.userMobile" placeholder=""></el-input>
            </el-form-item>

            <el-form-item label="" prop="userEmail">
                <el-input v-model="saveForm.userEmail" placeholder=""></el-input>
            </el-form-item>

            <el-form-item label="">
                <el-radio-group v-model="saveForm.userValid">
                    <el-radio :label=true></el-radio>
                    <el-radio :label=false></el-radio>
                </el-radio-group>
            </el-form-item>

            <el-form-item label="" prop="userQuestion">
                <el-select v-model="saveForm.userQuestion" filterable clearable placeholder="">
                    <el-option v-for="(item,index) in questionOptions" :label="item.label" :key="index"
                               :value="item.value">
                    </el-option>
                </el-select>
            </el-form-item>

            <el-form-item label="" prop="userAnswer">
                <el-input v-model="saveForm.userAnswer" placeholder=""></el-input>
            </el-form-item>


            <el-form-item label="">

                <el-transfer v-model="saveForm.roleIds" :data="roleList" filterable :titles="roleTitles"></el-transfer>

            </el-form-item>

            <el-form-item>
                <el-button type="primary" @click.native="save" :loading="saveLoading"></el-button>
                <el-button @click.native="handleBack(true)"></el-button>
            </el-form-item>


        </el-form>
        </el-col>

    </section>
</template>

<script>
    import baseUtil from '../../../js/baseUtil'
    import dateUtil from '../../../js/dateUtil'
    import {initialize, getSysUser, save, exisUser} from './userConfig.api'

    export default {
      data () {
        var validateUser = (rule, value, callback) => {
          if (value === '') {
            callback(new Error(''))
          } else {
            let para = {userId: this.userId}
            if (!this.userId) {
              para.userId = ''
            }
            exisUser(para).then((res) => {
              if (res.success.userList.indexOf(value) !== -1) {
                callback(new Error(''))
              } else {
                callback()
              }
            })
          }
        }

        var validatePass = (rule, value, callback) => {
          if (value === '') {
            callback(new Error(''))
          } else {
            if (this.saveForm.confirmPassword !== '') {
              this.$refs.saveForm.validateField('confirmPassword')
            }
            callback()
          }
        }

        var validatePass2 = (rule, value, callback) => {
          if (value === '') {
            callback(new Error(''))
          } else if (value !== this.saveForm.displayPassword) {
            callback(new Error('!'))
          } else {
            callback()
          }
        }

        return {
          showPassWord: true,

          saveForm: {
            commonFlag: 'add',
            userId: '', // 
            userName: '', // 
            userDescrip: '', // 
            userPassword: '', // 
            displayPassword: '', // 
            confirmPassword: '', // 
            userSex: true, // 
            userTel: '', // 
            userMobile: '', // 
            userEmail: '', // 
            userValid: true, // 
            userQuestion: '', // 
            userAnswer: '', // 
            roleIds: [], // 
            curUserId: ''
          },

          saveLoading: false,

          questionOptions: [
            {'value': '', 'label': ''},
            {'value': '', 'label': ''},
            {'value': '', 'label': ''}

          ],

          roleList: [],

          roleTitles: ['', ''],

          // 
          saveFormRules: {
            userId: [
              {required: true, message: '', trigger: 'blur'},
              {min: 4, message: '4', trigger: 'blur'},
              {validator: validateUser, trigger: 'blur'}
            ],
            userName: [
              {required: true, message: '', trigger: 'blur'}
            ],
            userDescrip: [
              {required: true, message: '', trigger: 'blur'}
            ],
            displayPassword: [
              {required: true, message: '', trigger: 'blur'},
              {min: 6, message: '6', trigger: 'blur'},
              {validator: validatePass, trigger: 'blur'}
            ],
            confirmPassword: [
              {required: true, message: '', trigger: 'blur'},
              {min: 6, message: '6', trigger: 'blur'},
              {validator: validatePass2, trigger: 'blur'}
            ],
            userQuestion: [
              {required: true, message: '', trigger: 'blur'}
            ],
            userAnswer: [
              {required: true, message: '', trigger: 'blur'}
            ],
            userEmail: [
              {type: 'email', message: '', trigger: 'blur'}
            ]
          }
        }
      },
      methods: {
        // 
        handleBack: function (flag) {
          if (flag) {
            this.$confirm('', '', {}).then(() => {
              this.$emit('backConfig')
            }).catch(e => {
            })
          } else {
            this.$emit('backConfig')
          }
        },

        getSysUser: function () {
          if (this.userId) {
            this.showPassWord = false
            this.saveForm.commonFlag = 'edit'
            let para = {userId: this.userId}
            getSysUser(para).then((res) => {
              if (res.error) {
                this.$message({
                  showClose: true,
                  message: res.error,
                  type: 'error'
                })
              } else {
                this.saveForm.curUserId = res.success.saveForm.userId
                this.saveForm.userId = res.success.saveForm.userId
                this.saveForm.userPassword = res.success.saveForm.userPassword
                this.saveForm.userName = res.success.saveForm.userName
                this.saveForm.userSex = res.success.saveForm.userSex
                this.saveForm.userTel = res.success.saveForm.userTel
                this.saveForm.userMobile = res.success.saveForm.userMobile
                this.saveForm.userEmail = res.success.saveForm.userEmail
                this.saveForm.userDescrip = res.success.saveForm.userDescrip
                this.saveForm.userValid = res.success.saveForm.userValid
                this.saveForm.userManager = res.success.saveForm.userManager
                this.saveForm.lastTime = res.success.saveForm.lastTime
                this.saveForm.userAmount = res.success.saveForm.userAmount
                this.saveForm.userQuestion = res.success.saveForm.userQuestion
                this.saveForm.userAnswer = res.success.saveForm.userAnswer
                this.saveForm.createDate = res.success.saveForm.createDate
                this.saveForm.updateDate = res.success.saveForm.updateDate
                this.saveForm.creator = res.success.saveForm.creator
                this.saveForm.modifier = res.success.saveForm.modifier
                this.saveForm.roleIds = res.success.roleIds
              }
            })
          }
        },

        // 
        initialize: function () {
          initialize().then((res) => {
            if (res.error) {
              this.$message({
                showClose: true,
                message: res.error,
                type: 'error'
              })
            } else {
              this.roleList = res.success.roleList
            }
          })
        },

        save: function () {
          this.$refs.saveForm.validate((valid) => {
            if (valid) {
              this.$confirm('', '', {}).then(() => {
                this.saveLoading = true

                const currentUser = JSON.parse(sessionStorage.getItem('user'))

                if (this.showPassWord) {
                  this.saveForm.creator = currentUser.userId
                  this.saveForm.userPassword = baseUtil(this.saveForm.displayPassword)
                  this.saveForm.createDate = dateUtil.formatDate.format(new Date(), 'yyyy-MM-dd hh:mm:ss')
                } else {
                  this.saveForm.createDate = dateUtil.formatDate.format(new Date(this.saveForm.createDate), 'yyyy-MM-dd hh:mm:ss')
                  this.saveForm.lastTime = dateUtil.formatDate.format(new Date(this.saveForm.lastTime), 'yyyy-MM-dd hh:mm:ss')
                }

                this.saveForm.modifier = currentUser.userId
                this.saveForm.updateDate = dateUtil.formatDate.format(new Date(), 'yyyy-MM-dd hh:mm:ss')

                save(this.saveForm).then((res) => {
                  this.saveLoading = false
                  if (res.error) {
                    this.$message({
                      showClose: true,
                      message: res.error,
                      type: 'error'
                    })
                  } else {
                    this.$message({
                      showClose: true,
                      message: '',
                      type: 'success'
                    })
                    this.$refs['saveForm'].resetFields()
                    this.handleBack(false)
                  }
                }).catch(e => {
                })
              }).catch(e => {
              })
            }
          })
        }

      },
      props: ['userId'],
      mounted () {
        this.initialize()
        this.getSysUser()
      }

    }
</script>


