<template>
    <section v-if="showConfig">
        <!----------------------------------------------------------------------------------------------------->
        <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">

            <el-form :inline="true" :model="filters" ref="filters">

                <el-form-item label="" prop="roleNameQuery">
                    <el-input v-model="filters.roleNameQuery" placeholder="">
                    </el-input>
                </el-form-item>

                <el-form-item>
                    <el-button type="primary" v-on:click="pagingList()" size="small" plain><i
                            class="fa fa-search fa-lg"></i> 


                    </el-button>
                </el-form-item>

                <el-form-item>
                    <el-button type="success" v-on:click="handleReset()"
                               size="small" plain><i class="fa fa-refresh fa-lg"></i> 


                    </el-button>
                </el-form-item>

                <el-form :inline="true">
                    <el-form-item>
                        <el-button type="success" @click="handleSave('')" icon="el-icon-plus" size="small"
                                   title="" plain>
                            


                        </el-button>
                    </el-form-item>


                </el-form>

            </el-form>


        </el-col>
        <!----------------------------------------------------------------------------------------------------->


        <!-----------------------------------------tab_role---------------------------------------------->
        <el-table :data="dataList" :height="tableHigth" v-loading="listLoading" @selection-change="selsChange"
                  highlight-current-row stripe border resizable>
            <el-table-column type="selection"></el-table-column>
            <el-table-column type="index" label="" width="80"></el-table-column>
            <el-table-column prop="roleName" show-overflow-tooltip label="" label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column prop="roleDesc" show-overflow-tooltip label="" label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column prop="createDate" show-overflow-tooltip label="" label-class-name="el-tooltip"
                             :formatter="formatDate">
            </el-table-column>
            <el-table-column prop="updateDate" show-overflow-tooltip label="" label-class-name="el-tooltip"
                             :formatter="formatDate">
            </el-table-column>
            <el-table-column prop="creator" show-overflow-tooltip label="" label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column prop="modifier" show-overflow-tooltip label="" label-class-name="el-tooltip">
            </el-table-column>
            <el-table-column label="" width="140" header-align="center" align="center">
                <template slot-scope="scope">
                    <el-button-group>
                        <el-button size="small" type="primary"
                                   @click="handleSave(scope.row.roleId)" icon="el-icon-edit" title=""></el-button>
                        <el-button size="small" type="danger"
                                   @click="handleDel(scope.$index, scope.row)" icon="el-icon-delete" title=""></el-button>
                    </el-button-group>
                </template>
            </el-table-column>
        </el-table>
        <!-----------------------------------------tab_role---------------------------------------------->


        <!----------------------------------------------------------------------------------------------------->
        <el-col :span="24" class="toolbar">
            <el-button type="danger" @click="batchRemove" :disabled="this.sels.length===0">


            </el-button>
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :page-sizes="[10,15,25,50,100]"
                    :page-size=filters.pageSize
                    :current-page.sync="resetIndex"
                    layout="total, sizes, prev, pager, next, jumper"
                    :total="total"
                    style="float:right;">
            </el-pagination>
        </el-col>
        <!----------------------------------------------------------------------------------------------------->


    </section>

    <section v-else-if="showSave">
        <component @backConfig="backConfig" :roleId="roleIdQuery" v-bind:is=roleSave>
        </component>
    </section>
</template>


<script>
  import tableHeight from '../../../js/heightUtil'
  import dateUtil from '../../../js/dateUtil'
  import { pagingList, remove } from './roleConfig.api'
  import roleSave from './roleSave'

  export default {
    data () {
      return {
        // 
        roleSave: roleSave,
        roleIdQuery: '',
        showConfig: true,
        showSave: true,

        dataList: [], // 
        listLoading: false, // list
        tableHigth: '', // 
        sels: [], // 
        total: 0, // 
        resetIndex: 1, // 
        // 
        filters: {
          pageIndex: 0,
          pageSize: 15,
          roleNameQuery: ''
        }
      }
    },

    methods: {

      // 
      formatDate: function (row, column) {
        return (!row[column.property] || row[column.property] === '') ? '' : dateUtil.formatDate.format(new Date(row[column.property]), 'yyyy-MM-dd hh:mm:ss')
      },

      /** **********************************************************************************************/
      // 
      pagingList: function () {
        this.listLoading = true
        this.tableHigth = tableHeight(371)
        pagingList(this.filters).then((res) => {
          if (res.error) {
            this.$message({
              showClose: true,
              message: res.error,
              type: 'error'
            })
          } else {
            this.total = res.success.total
            this.dataList = res.success.rows
          }
          this.listLoading = false
        })
      },
      // 
      handleReset () {
        this.$refs['filters'].resetFields()
        this.filters.pageIndex = 0
        this.filters.pageSize = 15
        this.resetIndex = 1
        this.pagingList()
      },
      // 
      handleSizeChange (val) {
        this.filters.pageSize = val
        this.handleCurrentChange(1)
      },
      //  
      handleCurrentChange (val) {
        if (val === 1) {
          this.filters.pageIndex = 0
        } else {
          this.filters.pageIndex = (val - 1) * (this.filters.pageSize)
        }
        this.pagingList()
      },
      /** **********************************************************************************************/

      /** **************************************************************************************************/
      // 
      selsChange: function (sels) {
        this.sels = sels
      },
      handleDel: function (index, row) {
        this.$confirm('?', '', {
          type: 'warning'
        }).then(() => {
          let para = {deleteIds: row.roleId}
          remove(para).then((res) => {
            if (res.error) {
              this.$message({
                showClose: true,
                message: res.error,
                type: 'error'
              })
            } else {
              this.$message({
                showClose: true,
                message: '',
                type: 'success'
              })
            }
            this.pagingList()
          })
        })
      },
      batchRemove: function () {
        const ids = this.sels.map(item => item.roleId).toString()
        this.$confirm('', '', {
          type: 'warning'
        }).then(() => {
          let para = {deleteIds: ids}
          remove(para).then((res) => {
            if (res.error) {
              this.$message({
                showClose: true,
                message: res.error,
                type: 'error'
              })
            } else {
              this.$message({
                showClose: true,
                message: '',
                type: 'success'
              })
            }

            this.pagingList()
          })
        })
      },
      /** **************************************************************************************************/

      /** ***********************************************************************************************/
      handleSave: function (roleId) {
        this.roleIdQuery = roleId
        this.showConfig = false// 
        this.showSave = true// 
      },
      backConfig: function () {
        this.showConfig = true// 
        this.showSave = false// 
        this.pagingList()
      }

      /** ***********************************************************************************************/
    },

    created () {
      this.pagingList()
    }
  }
</script>

<style>
    .view-table label {
        color: #99a9bf;
    }

</style>


